body.error404 .error-404 {
  background: #f6f4f9;
  padding: 50px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #4f4d55;
  min-height: calc(100vh - 120px);
}
body.error404 .error-404__header, body.error404 .error-404__content {
  text-align: center;
  width: 100%;
  max-width: 496px;
  margin: 0 auto;
}
body.error404 .error-404 h1.error-404__title {
  font-weight: 700;
  line-height: 1em;
  font-size: 7.5rem;
}
@media (min-width: 992px) {
  body.error404 .error-404 h1.error-404__title {
    font-size: 12.5rem;
  }
}
body.error404 .error-404__subtitle {
  color: #4f4d55;
  margin-bottom: 12px;
}
body.error404 .error-404__content p {
  color: #4f4d55;
  margin-bottom: 20px;
}

