body.search h1 span {
  font-size: inherit;
}

body.search-results {
  display: block;
  margin-left: 0;
}
body.search-results main.site-main {
  padding-bottom: 40px;
  padding-top: 40px;
}
body.search-results main.site-main header.page-header {
  padding-top: 30px;
  padding-bottom: 30px;
}
@media (min-width: 992px) {
  body.search-results main.site-main header.page-header h1.page-title {
    font-size: 2.5rem;
    line-height: 2.625rem;
    text-transform: none;
  }
}
body.search-results main.site-main article {
  border-bottom: 1px solid #210044;
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
  padding-top: 20px;
  row-gap: 10px;
}
body.search-results main.site-main article:last-of-type {
  border: none;
  margin-bottom: 0;
  padding-bottom: 0;
}
body.search-results main.site-main article span {
  font-family: "Body Font", serif;
  font-size: 1.0625rem;
  font-weight: 700;
  letter-spacing: -0.01rem;
}
body.search-results main.site-main article header.entry-header {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}
body.search-results main.site-main article header.entry-header h2.entry-title a {
  font-family: "Body Font", serif;
  border-bottom: 2px solid #210044;
  color: #210044;
  display: inline;
  line-height: initial;
  padding-bottom: 2px;
  text-decoration: none;
  transition: all 0.2s ease;
}
body.search-results main.site-main article header.entry-header h2.entry-title a:hover {
  border-bottom-color: transparent;
}
body.search-results main.site-main article header.entry-header .entry-meta .posted-on a {
  text-decoration: underline;
}
body.search-results main.site-main article header.entry-header .entry-meta .posted-on a:hover {
  text-decoration: none;
}
body.search-results main.site-main article header.entry-header .entry-meta .posted-on a time {
  color: #662d91;
}
body.search-results main.site-main article .entry-summary {
  display: flex;
  column-gap: calc(var(--elev-gutter-x) * 0.2);
  row-gap: calc(var(--elev-gutter-x) * 0.2);
}
body.search-results main.site-main article .entry-summary .card__link {
  flex: 0 0 250px;
}
body.search-results main.site-main article footer.entry-footer {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}
body.search-results main.site-main article footer.entry-footer .edit-link {
  display: block;
}
body.search-results main.site-main article footer.entry-footer .edit-link a.post-edit-link {
  font-family: "Body Font", serif;
  align-items: center;
  background-color: transparent;
  border-radius: 0;
  border: none;
  color: #662d91;
  column-gap: 10px;
  display: inline-flex;
  font-size: 0.8125rem;
  font-weight: 700;
  letter-spacing: 0.008125rem;
  line-height: 1.15;
  min-height: 25px;
  padding: 5px 0;
  position: relative;
  text-decoration: underline;
  text-transform: none;
  transition: all 0.2s ease;
}
body.search-results main.site-main article footer.entry-footer .edit-link a.post-edit-link::before {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.04495 3.48654L2.52844 2L8 7.48284L2.49419 13L1 11.5027L4.98807 7.50644L1.04495 3.48654Z' stroke='%23BB133E' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M7.04495 3.48654L8.52844 2L14 7.48284L8.49419 13L7 11.5027L10.9881 7.50644L7.04495 3.48654Z' stroke='%23BB133E' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-flex;
  height: 15px;
  transition: all 0.2s ease;
  width: 15px;
}
body.search-results main.site-main article footer.entry-footer .edit-link a.post-edit-link:hover {
  color: inherit;
  text-decoration: none;
}
body.search-results main.site-main article footer.entry-footer .edit-link a.post-edit-link:hover::before {
  background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.04495 3.48654L2.52844 2L8 7.48284L2.49419 13L1 11.5027L4.98807 7.50644L1.04495 3.48654Z' stroke='%23002147' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M7.04495 3.48654L8.52844 2L14 7.48284L8.49419 13L7 11.5027L10.9881 7.50644L7.04495 3.48654Z' stroke='%23002147' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}
body.search-results main.site-main article footer.entry-footer .cat-links a {
  text-decoration: underline;
  color: #662d91;
}
body.search-results main.site-main article footer.entry-footer .cat-links a:hover {
  text-decoration: none;
}
body.search-results main.site-main .navigation.posts-navigation {
  padding-top: 50px;
}
body.search-results main.site-main .navigation.posts-navigation .nav-links .nav-previous a,
body.search-results main.site-main .navigation.posts-navigation .nav-links .nav-next a {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  font-family: "Heading Font", sans-serif;
  align-items: center;
  background-color: transparent;
  border-radius: 40px;
  border: none;
  color: #4f4d55;
  column-gap: 10px;
  display: inline-flex;
  font-size: 0.875rem;
  letter-spacing: 0rem;
  line-height: 1.3;
  text-transform: none;
  transition: all 0.2s ease;
  text-decoration: none;
}
body.search-results main.site-main .navigation.posts-navigation .nav-links .nav-previous a::selection,
body.search-results main.site-main .navigation.posts-navigation .nav-links .nav-next a::selection {
  background-color: var(--wp--preset--color--ui-foreground-primary-text-headings);
  color: white;
}

body.search-no-results header.page-header {
  padding-top: 30px;
  padding-bottom: 30px;
}
@media (min-width: 992px) {
  body.search-no-results header.page-header h1.page-title {
    font-size: 2.5rem;
    line-height: 2.625rem;
    text-transform: none;
  }
}
body.search-no-results .page-content {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  padding: 0 0 40px;
}
body.search-no-results .search-form {
  display: flex;
  gap: 30px;
}
body.search-no-results .search-form label {
  flex: 0 0 300px;
}
body.search-no-results .search-form .search-field {
  font-family: "Body Font", serif;
  border-radius: 2px;
  border: 0.5px solid #bcb9b9;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0.24px;
  line-height: 1.2;
  padding: 15px 20px;
  width: 100%;
}
body.search-no-results .search-form .search-submit {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  font-family: "Heading Font", sans-serif;
  align-items: center;
  background-color: transparent;
  border-radius: 40px;
  border: none;
  color: #4f4d55;
  column-gap: 10px;
  display: inline-flex;
  font-size: 0.875rem;
  letter-spacing: 0rem;
  line-height: 1.3;
  text-transform: none;
  transition: all 0.2s ease;
  text-decoration: none;
}
body.search-no-results .search-form .search-submit::selection {
  background-color: var(--wp--preset--color--ui-foreground-primary-text-headings);
  color: white;
}